$clr-services-title: #2d6a88;

$clr-card-title-1: #259ca0;
$clr-card-title-2: #2a6c8a;
$clr-card-text: #2d3436;

.services {
  margin: 12em 1.6em 5em 1.6em;
  color: $clr-services-title;
  display: flex;
  justify-content: center;

  &-container {
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;

    @media screen and (min-width: 768px) {
      grid-template-columns: 1fr 1fr;
      gap: 2em;
      .services__title {
        grid-column: 1/3;
        font-size: 6em;
      }
    }
    @media screen and (min-width: 1024px) {
      width: 90%;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      gap: 3em;
      max-width: 130rem;
      justify-content: center;
      .services__title {
        grid-column: 1/5;
      }
    }
  }

  &__title {
    font-size: 3em;
    font-family: 800;
    text-align: center;
  }
  &-card {
    padding-top: 4em;
    border-radius: 10px;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    padding-inline: 1.6em;
    max-width: 25em;
    border-bottom: 4px solid $clr-card-title-1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 5em 0;
    box-shadow: 0px 0px 32px 0px rgba(59, 61, 109, 0.15);
    transition: all 300ms ease-in-out;

    &__img {
      width: 64px;
      height: 64px;
      border-radius: 50px;

      margin-bottom: 2.8em;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;

      &::before {
        background-color: $clr-card-title-1;
        content: "";
        height: 3px;
        width: 20px;
        position: absolute;
        top: 0;
        right: 0;
        transform: rotate(135deg) translate(-100%, -200%);
      }
      &::after {
        background-color: $clr-card-title-1;
        content: "";
        height: 3px;
        width: 20px;
        position: absolute;
        bottom: 0;
        left: 0;
        transform: rotate(135deg) translate(100%, 200%);
      }
    }
    &__title {
      color: $clr-card-title-1;
      text-align: center;
      font-size: 1.6em;
      font-weight: 700;
      margin-top: 1em;
    }
    &__text {
      text-align: center;
      font-size: 1.3em;
      line-height: 1.4;
      color: $clr-card-text;
    }
    &__more {
      margin-top: 2em;
      margin-bottom: 3em;
      font-weight: 400;
      font-size: 1.3em;
      text-transform: uppercase;
      color: $clr-card-title-1;
      outline: none;
      border: none;
      background-color: inherit;
      &:focus {
        outline: none;
      }
    }
  }
}
.services-card:hover {
  transform: scale(1.05);
}

.services-card__odd {
  background: linear-gradient(
    315deg,
    rgba(76, 173, 177, 1) 50%,
    $clr-card-title-1 50%
  );
}

.services-card__even {
  background: linear-gradient(315deg, #50869f 50%, $clr-card-title-2 50%);
  &::before,
  &::after {
    background: $clr-card-title-2;
  }
  + .services-card__title {
    color: $clr-card-title-2;
  }
}

button.even {
  color: $clr-card-title-2;
}

div.even {
  border-bottom: 4px solid $clr-card-title-2;
}
