$clr-white: #fff;
$clr-btn-bg: #164055;
$clr-btn-shadow: rgba(19, 57, 76, 0.75);
h1,
h3 {
  color: $clr-white;
  margin: 0;
}

h3 {
  font-size: 1.4rem;
  font-weight: 300;
  margin-bottom: 0.6em;
}

h1 {
  font-size: 2.4rem;
  font-weight: 800;
}

.hero {
  
  background: url("../asssets/img/Header-image.png") no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  &-text {
    padding: 0 1.6rem;
    width: 90%;
    &__h3:first-of-type {
      padding-top: 8em;
      padding-bottom: 0.5em;
      margin: 0;
    }
    &__h1 {
      position: relative;
      height: 1em;
      overflow: hidden;
      width: 90%;
      margin: 0;
      padding-bottom: 0.5em;
      overflow: hidden;
    }
    &__h3:nth-of-type(2) {
      font-weight: 600;
    }
  }
}

.btn {
  font-size: 1.4rem;
  font-weight: 600;
  color: $clr-white;
  background: $clr-btn-bg;
  border: none;
  padding: 1.5em 3.5em;
  margin: 3.5em 0 11em 1.6rem;
  border-radius: 1em;
  max-width: 20rem;
  box-shadow: 0px 17px 22px 0px rgba(19, 57, 76, 0.75);
  &:focus {
    outline: none;
  }
}
.btn > a {
  color: #fff;
}
.words {
  margin-left: 240px;
  width: 35%;
}

.words-item {
  display: block;
  height: 100%;
  padding: 10px 0;
  width: fit-content;
  opacity: 1;
  animation: spin_words 7s infinite;
}

@keyframes spin_words {
  25% {
    transform: translateY(-90%);
  }
  
  50% {
    transform: translateY(-190%);
  }
  75% {
    transform: translateY(-290%);
  }
  95% {
    transform: translateY(-390%);
  }
  100%{
    transform: translateY(-390%)
  }
}

@media (min-width: 43em) {
  .hero {
    padding-left: 2rem;

    &-text__h3 {
      font-size: 2.4rem;
      padding-left: 0;
    }
    &-text__h1 {
      font-size: 4.5rem;
      padding-left: 0;
    }
  }
  .hero-text__h3:first-of-type {
    padding-top: 3em;
  }

  .btn {
    font-size: 1.8rem;
    max-width: 26rem;
    margin-left: 0;
    transition: all 300ms ease-in-out;
    cursor: pointer;
    &:hover {
      transform: scale(0.9);
    }
  }
}

@media (min-width: 62em) {
  .hero {
    padding-left: 5rem;

    &-text__h3 {
      font-size: 3.2rem;
    }
    &-text__h1 {
      font-size: 6rem;
    }
  }
  .hero-text__h3:first-of-type {
    padding-top: 2em;
  }

  .btn {
    font-size: 1.8rem;
    max-width: 26rem;
  }
}

@media (min-width: 87em) {
  .hero {
    padding: 0 20rem;
  }
}
