$clr-offer-title: #2d6a88;
$clr-offer-text: #4c5d65;
$bg-offers: #edf3f6;

.offers {
  background-color: $bg-offers;
  padding: 5rem 1.6rem;
  display: grid;
  justify-content: center;
  align-items: center;
  gap: 1em;
}

.offer {
  display: grid;
  max-width: 47rem;
  gap: 1.5em;

  &__title {
    font-size: 2.1em;
    font-weight: 800;
    color: $clr-offer-title;
  }
  &__text {
    font-size: 1.2em;
    font-weight: 400;
    color: #4c5d65;
    line-height: 1.7;
  }
  &__img {
    width: 100%;
    border-top-right-radius: 50px;
    border-bottom-left-radius: 50px;
    box-shadow: 0px 0px 10px 5px rgb(192, 192, 192);
  }
}
@media (min-width: 43em) {
  .offer__title {
    font-size: 4.8rem;
  }
  .offer__text {
    font-size: 1.6rem;
  }
}

@media (min-width: 62em) {
  .offer__title {
    font-size: 4.5rem;
  }
  .offers {
    grid-template-columns: 1fr 1fr;

    justify-items: center;
    align-items: center;
  }
  .offer:last-of-type > img {
    grid-row: 1/2;
  }
}

@media (min-width: 87em) {
  .offer__title {
    font-size: 6rem;
  }
}
