$clr-title: #00283b;
$clr-text: #2d6a88;
$clr-accent: #ee2323;
$clr-button: #164055;
$clr-send: #18c801;
$bg-send: #e7fae5;
$clr-section-shadow: #e1e7ea;

.contact {
  position: relative;
  z-index: 0;
  grid-column: 1/4;
  padding: 5rem 2.6rem;

  &-title {
    color: $clr-title;
    font-size: clamp(2.2rem, 2.8rem, 4rem);
    font-weight: 700;
  }
  &-form {
    display: flex;
    flex-direction: column;
    max-width: 62em;
    margin: 0 auto;
  }
  &-form > input {
    padding: 0.9em;
    color: $clr-text;
    font-size: 1.4rem;
    margin: 1rem 0;
    font-weight: 500;
    border: 2px solid $clr-text;
  }
  &-form > input:focus {
    color: $clr-accent;
    border: 2px solid $clr-accent;
    outline: none;
  }
}

#form__message {
  height: auto;
  padding: 0.9em;
  color: $clr-text;
  font-size: 1.4rem;
  margin: 1rem 0;
  font-weight: 500;
  border: 2px solid $clr-text;
  resize: none;
}

#form__message:focus {
  color: $clr-accent;
  border: 2px solid $clr-accent;
  outline: none;
}

.form-submit {
  outline: none;
  border: none;
  background-color: $clr-button;
  color: #fff;
  font-size: 1.6rem;
  font-weight: 500;
  padding: 1.25em 0;
  border-radius: 10px;
  margin: 4rem 0 2.5rem 0;
  transition: all 300ms ease-in-out;
  cursor: pointer;
  &:hover {
    transform: scale(0.95);
  }
}

.form-popup {
  color: #18c801;
  background-color: $bg-send;
  font-size: 1.4rem;
  font-weight: 500;
  padding: 1em;
  text-align: center;
  display: none;
}
.form-popup__display {
  display: block;
}

@media (min-width: 87em) {
  .contact {
    height: 529.2px;
    position: absolute;
    top: 2em;
    right: 32em;
    grid-column: 1/4;
    background: #fff;
    padding: 0;
    display: block;
    box-shadow: #acaaaa 0 2px 5px -1px;
  }
  .contact-form {
    padding: 2rem;
    padding-top: 1em;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 5rem 7rem 7rem 15rem 7rem;
    gap: 1rem;
  }
  .contact-title {
    display: block;
    margin: 0;
    padding: 0;
    margin-top: 1rem;
    grid-column: 1/3;
    grid-row: 1/2;
  }
  .form-name {
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .form-tel {
    grid-column: 1/2;
    grid-row: 3/4;
  }
  .form-email {
    grid-column: 2/3;
    grid-row: 2/3;
  }
  .form-message {
    grid-column: 1/3;
    grid-row: 4/5;
  }
  .form-subject {
    grid-column: 2/3;
    grid-row: 3/4;
  }
  .form-submit {
    grid-column: 2/3;
    grid-row: 5/6;
    width: 100%;
    align-self: center;
    justify-self: end;
  }
  .form-popup {
    grid-column: 1/2;
    grid-row: 5/6;
    align-self: center;
    justify-self: end;
    margin-bottom: 1rem;
  }
}
