$clr-header-item: #393d6a;
$clr-list-bg: #194b64;
$clr-white: #fff;
$clr-list-decoration: #336c8a67;
$clr-menu-text: #269c9e;

.header {
  background-color: $clr-white;

  &-item {
    margin: 0;
    font-size: 1.2rem;
    font-weight: 600;
    align-self: baseline;
    color: $clr-header-item;
    padding: 1.6em 0;
    padding-left: 2rem;
  }
  &-hours {
    width: 70%;
    > span {
      font-weight: 400;
    }
  }
}

.header-pre-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 2px 0px;
}

.nav-wrapper {
  height: 10rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.nav-list {
  list-style: none;
  background-color: $clr-list-bg;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  width: 80%;
  text-align: center;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 1;
  &__item {
    width: 100%;
    padding: 2em 0;
    border-bottom: 2px solid $clr-list-decoration;
    & a {
      color: $clr-white;
      font-weight: 700;
      font-size: 1.4rem;
      text-transform: uppercase;
      position: relative;
    }
  }
  &__item:last-of-type {
    border: none;
  }
}
.nav-list__item > a {
  transition: all 300ms ease-in-out;
}
.nav-list__item > a:hover::before {
  position: absolute;
  content: "";
  top: 0%;
  left: 50%;
  width: 1.2em;
  height: 2px;
  margin-top: -1em;
  background-color: $clr-menu-text;
  transform: rotate(120deg);
}
.nav-list__item > a:hover::after {
  position: absolute;
  content: "";
  bottom: 0%;
  left: 30%;
  width: 1.2em;
  height: 2px;
  margin-bottom: -1em;
  background-color: $clr-menu-text;
  transform: rotate(120deg);
}

.nav-link__img {
  padding: 1.6em 0 1.6em 1.6em;
}

.show-menu {
  display: flex;
}
.lock-scroll {
  overflow: hidden;
}
.hamburger:focus {
  outline: none;
}

@media (min-width: 43em) {
  .nav-link__img {
    margin-left: 2rem;
    padding: 0;
  }

  .nav-list {
    margin: 0;
  }

  .header-phone {
    font-weight: 400;
  }
  .header-mail {
    margin-right: 5em;
    font-weight: 400;
  }
}

@media (min-width: 62em) {
  .nav-link__img {
    margin-left: 5rem;
    padding: 0;
  }
  .header-hours {
    width: 60%;
  }

  .header-item {
    padding-left: 0;
  }

  button.hamburger {
    display: none;
  }

  .nav-list {
    position: relative;
    margin-right: 5rem;
    display: flex;
    width: 55%;
    max-width: 60em;
    flex-direction: row;
    justify-content: space-between;
    background-color: inherit;
    &__item {
      border: none;
      width: auto;
    }

    &__item > a {
      color: hsl(0, 0%, 50%);
      transition: color 300ms ease-in-out;
    }

    &__item > a:active,
    &__item > a:hover {
      color: $clr-menu-text;
    }
  }
  .header-pre-nav {
    padding: 0 5rem;
  }
}
@media (min-width: 87em) {
  .header-pre-nav {
    padding: 0 20rem;
  }
  .nav-link__img {
    margin-left: 20rem;
    padding: 0;
  }
  .nav-list {
    margin-right: 20rem;
  }
}
