$title-clr: #2d6a88;

.carousel {
  margin: 10rem 0;
  &-title {
    margin-bottom: 3em;
    font-size: 3em;
    text-align: center;
    color: $title-clr;
    font-weight: 800;
  }
}

.swiper-container {
  height: 10rem;
  justify-content: baseline;
}
.swiper-slide {
  text-align: center;
}
@media (min-width:43em){
  .carousel-title {
    font-size: 4.5em;
  }
  
}
@media (min-width:62em){
  .carousel-title {
    font-size: 6em;
  }
  
}