$clr-text: #00283b;
.location {
  color: $clr-text;

  &-picture {
    width: 100%;
  }
  &-bar {
    display: grid;
    grid-template-columns: 20% 80%;
    justify-content: start;
    align-items: center;
    gap: 2em;
    padding: 1.5em 0;
  }
}
.location-map__iframe {
  width: 100%;
  height: 60rem;
}

//  bars
.bar-wrapper {
  margin: 1rem 0;
}

.bar-ico {
  justify-self: start;
  padding-left: 2em;
}
.bar-title {
  font-size: 1.2rem;
  font-weight: 400;
  letter-spacing: 0.1em;
}
.bar-text {
  font-weight: bold;
  margin: 0;
}

.location-on-map {
  display: none;
}

@media (min-width: 43em) {
  .location {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
  .location-map {
    grid-column: 1/4;
  }
}

@media (min-width: 62em) {
  .hidden {
    display: none;
  }
  .location-picture {
    display: none;
  }
  .location {
    position: relative;
  }
  .location-on-map {
    display: block;
    position: absolute;
    top: 0;
    right: 0;

    margin-top: 2em;
    margin-bottom: 1em;
    background-color: rgb(255, 255, 255);
    padding-bottom: 2em;
    box-shadow: #acaaaa 0 2px 5px -1px;
  }

  .bar-ico {
    padding-left: 2em;
  }
  .bar-wrapper-logo {
    padding: 2em 0 2em 2em;
  }
}
