$clr-bar-text: #2a6c8a;

.bar {
  color: $clr-bar-text;
  background-color: white;
  margin: -6rem auto 0 auto;
  width: 80%;
  max-width: 40rem;
  border-radius: 15px;
  padding-inline: 2em;
  box-shadow: 0px 0px 32px 0px rgba(59, 61, 109, 0.15);
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;

  @media screen and(min-width:62em) {
    width: unset;
    margin: -6rem 5rem 0 ;
    align-items: center;
    justify-items: center;
    max-width: 100%;
  }
  @media screen and(min-width:87em) {
    margin: -6rem 20rem 0 ;
    align-items: center;
    justify-items: center;
    
  }

  &-container {
    display: grid;
    grid-template-columns: 1fr;
    align-items: center;
    justify-items: center;
    gap: 5em;
    margin: 1em 0;

    @media screen and(min-width:62em) {
      grid-template-columns: repeat(3, 1fr);
    }
  }
}

.bar-card {
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  align-items: end;
  max-width: 30rem;

  margin: 2em 0;
  gap: 1em;

  @media (min-width: 62em) {
    align-items: center;
    margin: 0;
    padding: 0;
    gap: 2em;
    display: grid;
    grid-template-columns: 1fr 4fr;
  }

  &__title {
    font-size: 1.8rem;
    font-weight: 700;
    margin-bottom: 0.5em;
    margin-top: 1em;
    text-align: center;
    @media screen and(min-width:62em) {
      text-align: start;
    }
  }
  &__text {
    font-size: 1.4em;
    font-weight: 400;
    justify-content: center;
    text-align: center;
    @media screen and(min-width:62em) {
      text-align: start;
    }
  }
  &-mail {
    cursor: pointer;
  }
  &__icon {
    background-color: $clr-bar-text;
    width: 7rem;
    height: 7rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
