$clr-title: #2d6a88;

.about {
  margin: 5rem 1.6rem 10rem 1.6rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  &-title {
    font-size: 3rem;
    font-weight: 800;
    color: $clr-title;
  }
  &-list {
    padding: 0;
    list-style-type: none;
    color: $clr-title;
    font-size: 1.1rem;
    font-weight: 600;
    display: flex;
    flex-direction: column;
  }
}
.list-item {
  background-color: white;
  padding: 1em 3em;
  margin: 1rem 0;
  border-radius: 15px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.item-icon {
  padding-right: 1.5em;
}

@media (min-width: 43em) {
  .about-title {
    font-size: 4.5rem;
  }
}
@media (min-width: 62em) {
  .about-list{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap:2em
  }
  .about-title {
    font-size: 6rem;
  }
}
@media (min-width: 87em) {
  .about-list{
    font-size: 2.4rem;
  }
}
