$clr-prim: #2d6a88;

.recomendations {
  margin: 0 1.6rem 12.5rem 1.6rem;
  &-title {
    color: $clr-prim;
    text-align: center;
    font-size: 3rem;
    font-weight: 800;
  }
}

.carousel-2 {
  height: 25rem;
}

@media (min-width: 43em) {
  .recomendations-title {
    font-size: 4.5rem;
  }
  
}
@media (min-width: 62em) {
  .recomendations-title {
    font-size: 6rem;
  }
  
}
