$clr-title: #fff;
$clr-text: rgba(255, 255, 255, 0.7);
$clr-line: rgba(255, 255, 255, 0.2);
$clr-bg: #164055;

.footer {
  background-color: $clr-bg;
  padding: 0 2.6rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  &-title {
    font-size: 2rem;
    font-weight: 700;
    color: $clr-title;
    padding-top: 2em;
  }
  &-list {
    list-style-type: none;
    margin-bottom: 7em;
    padding: 0;
  }
  &-list > li > a {
    color: $clr-text;
    font-size: 1.4rem;
    font-weight: 400;
    transition: color 300ms ease-in-out;
  }
  &-list > li {
    padding: 0.75em 0;
  }
  &-link {
    width: 100%;
    text-decoration: none;
    color: $clr-title;
    font-size: 1.2rem;
    display: block;

    text-align: center;
    border-top: 1px solid $clr-line;
    grid-column: 1/3;
    transition: color 300ms ease-in-out;
    &:hover {
      color: hsl(0, 0%, 60%);
    }
  }
}

.footer-list > li > a:hover {
  
  color: hsl(0, 0%, 60%);
}

.display-item {
  display: none;
}

@media (min-width: 62em) {
  .display-item {
    display: inline;
  }
}
